const handleSpanMethod = ({ row, column, rowIndex, columnIndex }) => {
  if (columnIndex === 0) {
    const currentRow = tableData.value[rowIndex];
    const prevRow = tableData.value[rowIndex - 1];
    if (rowIndex > 0 && currentRow.listTitle === prevRow.listTitle) {
      return {
        rowspan: 0,
        colspan: 0,
      };
    } else {
      let rowspan = 1;
      for (let i = rowIndex + 1; i < tableData.value.length; i++) {
        if (tableData.value[i].listTitle === currentRow.listTitle) {
          rowspan++;
        } else {
          break;
        }
      }
      return {
        rowspan,
        colspan: 1,
      };
    }
  }
};
//导出table

//定义导出Excel表格事件
const outTable = () => {
  let xlsxParam = { raw: true };
  /* 从表生成工作簿对象 */
  let wb = XLSX.utils.table_to_book(
    document.querySelector("#out-table"),
    xlsxParam
  );
  /* 获取二进制字符串作为输出 */
  let wbout = XLSX.write(wb, {
    bookType: "xlsx",
    bookSST: true,
    type: "array",
  });
  try {
    FileSaver.saveAs(
      //Blob 对象表示一个不可变、原始数据的类文件对象。
      //Blob 表示的不一定是JavaScript原生格式的数据。
      //File 接口基于Blob，继承了 blob 的功能并将其扩展使其支持用户系统上的文件。
      //返回一个新创建的 Blob 对象，其内容由参数中给定的数组串联组成。
      new Blob([wbout], { type: "application/octet-stream" }),
      //设置导出文件名称
      "sheetjs.xlsx"
    );
  } catch (e) {
    if (typeof console !== "undefined") console.log(e, wbout);
  }
  return wbout;
};

const clearTableShow = ref(false);
const showClearTable = () => {
  const mergedData: any = {};

  clearTableData.value.forEach((item) => {
    const figue = item.figue;
    if (mergedData[figue]) {
      mergedData[figue].amount += parseInt(item.amount);
    } else {
      mergedData[figue] = JSON.parse(JSON.stringify(item)); // 使用深拷贝复制对象
    }
  });

  // 将对象转换为数组形式
  const mergedArray = Object.values(mergedData);

  clearTableData.value = mergedArray;
  clearTableShow.value = true;
};
