const electricBox = ref("")
const buttonBox = ref("");
const buttonBoxNum = ref(0);
const buttonBoxNumAble = ref(true);
const pumpList = [
  { value: "vdn902", label: "机械泵VDN902", power: 0.5 },
  { value: "vdn903", label: "机械泵VDN903", power: 0.5 },
  { value: "vdn602", label: "机械泵VDN602", power: 2.2 },
  { value: "vdn301", label: "机械泵VDN301", power: 1.1 },
