  { value: "TDK2030", label: "20-30", power: 0.6 },
  { value: "TDK6055", label: "60-55", power: 3.3 },
];
const roughPumpList = [
  { value: "KYKYFF250_2000", label: "KYKY分子泵FF250/2000", power: 0.75 },
  { value: "KYKYFF-200_1300", label: "KYKY分子泵 FF-200/1300", power: 0.75 },
  { value: "KYKYFF-160_700", label: "KYKY分子泵 FF-160/700", power: 0.75 },

];
