]
//点击选择
const vacuoPumpAble = ref(true);
//型号选择
const vacuoPower = ref(0);
const vacuoPumpChange = () => {
  if (vacuoPump.value) {
    let index = pumpList.findIndex((item) => item.value == vacuoPump.value);
    vacuoPower.value = pumpList[index].power;
    vacuoPumpAble.value = false;
  } else {
    vacuoPumpAble.value = true;
    vacuoPumpNum.value = 0;
  }
};  
const calVacuo = computed(() => {
  return vacuoPower.value * vacuoPumpNum.value;
});
const roughPower = ref(0);
const roughPumpChange = () => {
  console.log(roughPump.value);
  if (roughPump.value) {
    let index = roughPumpList.findIndex(
      (item) => item.value == roughPump.value
    );
    roughPower.value = roughPumpList[index].power;
    roughPumpAble.value = false;
  } else {
    roughPumpAble.value = true;
    roughPumpNum.value = 0;
  }
};
const calRough = computed(() => {
  return roughPower.value * roughPumpNum.value;
});
const allPower = computed(() => {
  const totalPower = calVacuo.value + calRough.value + calTDK1.value + calTDK2.value + calServo1.value + calServo2.value;
  return totalPower + " kw";
});
const boatSourceChange = () => {
  if (boatSource.value) {
    boatSourceAble.value = false;
  } else {
    boatSourceAble.value = true;
    boatSourceNum.value = 0;
